.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: var(--content-width);
}

.section {
  margin: var(--section-margin) auto;
  width: 100%;
  height: auto;
  color: var(--color-text);
  font-family: "Poppins", sans-serif;
  // overflow: hidden;

  #section__icon {
    display: flex;
    margin-right: 5px;
    font-size: 20px;
    width: clamp(1.06rem, calc(0.95rem + 0.55vw), 1.38rem);

    path {
      fill: var(--primary-color2);
    }
  }

  #section__info {
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: var(--margin-i);
    overflow: hidden;
  }

  &__title {
    font-size: clamp(1.5rem, calc(1.32rem + 0.88vw), 2rem);
  }

  &__line {
    position: relative;
    margin-left: 10px;
    &:after {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      margin: auto;
      content: "";
      width: 100vw;
      height: 1px;
      background-color: var(--primary-color2-25);
    }
  }
}

.section--playground {
  margin-bottom: 200px;
  @media screen and (min-width: 1300px) {
    & {
      margin-bottom: 350px;
    }
  }
  .message {
    position: relative;
    overflow: hidden;
    animation-name: none;
    &:after {
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
      content: "";
      width: 100%;
      height: 100%;
      background-color: var(--primary-color2-25);
      transform-origin: left;
      animation-timing-function: linear;
      animation: loadBox 24s linear infinite;
    }
    &__text {
      position: relative;
      padding-right: 13px;
      &::after {
        content: "...";
        position: absolute;
        animation: loadDots 3s infinite;
      }
      @keyframes loadDots {
        0% {
          content: "";
        }
        33.3% {
          content: ".";
        }
        66.6% {
          content: "..";
        }
        99.9% {
          content: "...";
        }
      }
    }
  }
  #message__icon {
    transform-origin: center;
    transform-box: box;
    transform: rotate(40deg);
    animation: spin 6s linear infinite;
    @keyframes spin {
      0% {
        transform: rotate(0deg);
      }

      100% {
        transform: rotate(360deg);
      }
    }
  }

  @keyframes loadBox {
    from {
      transform: translateX(-100%);
    }
    to {
      transform: translateX(100%);
    }
  }
}

.contact {
  text-align: center;
  margin-bottom: 100px;
  @media screen and (min-width: 1300px) {
    & {
      margin-bottom: 200px;
    }
  }
  &__subtitle {
    position: relative;
    width: fit-content;
    margin-bottom: 0.5rem;
    margin-inline: auto;
    font-size: var(--fs-s);
    color: var(--primary-color2);
    #section__icon {
      position: absolute;
      right: 100%;
      top: 0;
      bottom: 0;
      margin-block: auto;
      max-width: 15px;
    }
  }

  &__title {
    margin-bottom: 1rem;
    font-size: clamp(2.19rem, calc(1.63rem + 2.77vw), 3.75rem);
    color: var(--color-text);
  }

  &__description,
  &__apologize {
    max-width: 600px;
    margin: auto;
    margin-bottom: 2rem;
    font-family: var(--open-s);
    font-size: var(--fs-m);
    color: var(--color-text-2);
  }

  a {
    position: relative;
  }

  .btn--contact {
    position: relative;
    margin-bottom: 100px;
    transition: var(--fast-transition);
    opacity: 1;
    &:after {
      position: absolute;
      top: 110%;
      left: 0;
      right: 0;
      content: "dariochic@gmail.com";
      margin: auto;
      font-size: 12px;
    }
    &:before {
      position: absolute;
      bottom: 0;
      left: 0;
      // margin: auto;
      content: "Sorry, last time, I promise 😅";
      color: var(--secondary-color2);
      transform: translate(-50%, -470%);
      width: 200%;
      opacity: 0;
      transition: var(--fast-transition);
      transition-delay: 100ms;
      cursor: default;
    }
  }
  .btn--contact.active {
    transform: translateY(150%);
    &:before {
      transform: translate(-25%, -470%);
      opacity: 1;
    }
  }
  .btn--contact.es {
    &:before {
      content: "Disculpa, ultima vez, lo prometo 😅";
    }
  }
  .btn--contact.en {
    &:before {
      content: "Sorry, last time, I promise 😅";
    }
  }

  &__apologize {
    display: none;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    width: 200%;
    color: var(--secondary-color2);
    opacity: 0;
    // transform: translate(-15%, 5px);
    transition: var(--fast-transition);
  }

  &__apologize.active {
    opacity: 1;
    // transform: translate(-60%, 5px);
    // @media screen and (min-width:770px) {
    //   &{
    // transform: translate(-90px, 5px);
    //   }
    // }
  }
}

.social {
  display: flex;
  justify-content: space-between;
  svg {
    width: 19px;
    cursor: pointer;
    path {
      stroke-width: 1.5px;
      stroke: var(--color-text-2);
      transition: var(--fast-transition);
      opacity: 0.7;
    }
    &:hover {
      path {
        stroke: var(--primary-color2);
        transform: scale(1) translateY(-6px);
        opacity: 1;
      }
    }
  }
}

.footer {
  max-width: 240px;
  text-align: center;
  margin-block: 30px 80px;
  @media screen and (min-width: 770px) {
    margin-block: var(--section-margin) 25px;
  }
  &__social {
    margin-bottom: 0.7rem;
  }
  &__copyright {
    font-family: var(--open-s);
    font-size: 14px;
    letter-spacing: -0.4px;
    color: var(--color-text-2);
    opacity: 0.8;
  }
}

@media screen and (min-width: 870px) {
  .social--footer {
    position: fixed;
    right: 0;
    bottom: 0;
    flex-direction: column;
    align-items: center;
    width: var(--general-padding);
    height: 40%;
    transform: translateY(100%);
    max-height: 300px;
    padding-bottom: 80px;
    animation: up 1s ease-in forwards;
    @keyframes up {
      from {
        opacity: 0;
        transform: translateY(100%);
      }
      to {
        opacity: 1;
        transform: translateY(0%);
      }
    }
    &:after {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      content: "";
      height: 70px;
      width: 1px;
      opacity: 0.7;
      background-color: var(--color-text-2);
    }
  }
}

@media screen and (min-width: 1200px) {
  .social--footer {
    width: calc((100vw - 1024px) / 2);
  }
}
