:root {
  --longer-transition: all 1s ease-in-out;
}

.modal {
  position: fixed;
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  visibility: hidden;
  background-color: transparent;
  transition: all 1s ease-in-out;
  &.modal--open {
    visibility: visible;
    .about {
      &-profile {
        opacity: 1;
        transform: translateY(0);
      }
      &-nav {
        opacity: 1;
        transform: translateX(0);
      }
      &-info {
        opacity: 1;
        transform: translateX(0);
      }
    }
  }
}

.about {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 640px 60px auto;
  width: 100%;
  height: 100%;
  overflow: auto;
  @media screen and (min-width: 770px) {
    & {
      width: 98%;
    }
  }
  &__list {
    margin: auto;
    list-style: none;
    text-align: left;
  }

  &__item {
    margin-bottom: 1.5rem;
  }

  &__subtitle {
    line-height: 1.5;
    font-family: var(--poppins);
    font-weight: 600;
    color: var(--color-text);
    font-size: 20px;
  }
  &-profile {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: start;
    height: 640px;
    padding: 1rem 2rem 0.7rem 2rem;
    text-align: center;
    background-color: var(--secondary-color);
    box-shadow: var(--box-shadow);
    transition: var(--longer-transition);
    transform: translateY(-100%);
    opacity: 0;
    button {
      cursor: pointer;
    }
    #about-profile__close {
      position: absolute;
      top: clamp(15px, 4%, 25px);
      left: clamp(15px, 6%, 25px);
      width: 22px;
      path {
        fill: var(--secondary-color2);
      }
    }
    &__img {
      margin-bottom: 2rem;
      margin: auto;
      width: 100%;
      img {
        width: 150px;
        border-radius: 50%;
        box-shadow: var(--box-shadow);
        border: 1px solid var(--primary-color2-50);
        transform-origin: top;
        transition: all 0.3s ease-in-out;
      }
      figcaption {
        font-family: var(--poppins);
        font-size: 1.5rem;
        font-weight: 600;
        color: var(--color-text);
      }
    }

    &__info {
      width: 100;
      max-width: 450px;
      height: 100%;
      max-height: 640px;
      font-family: var(--open-s);
      font-size: 14.5px;
      line-height: 1.3;
      color: var(--color-text-2);
      overflow: auto;
    }

    &__languages {
      display: flex;
      margin-top: 0.5rem;
    }

    &__language {
      margin-right: 2rem;
      text-align: center;
      .lang {
        margin-bottom: -0.15rem;
        font-family: var(--open-s);
        font-size: 14px;
        color: var(--color-text);
      }
      .level {
        font-family: var(--open-s);
        font-size: 12.5px;
        color: var(--primary-color2);
        font-weight: 200;
        font-style: italic;
      }
      .flag {
        margin-bottom: -0.33rem;
      }

      #profile__flag {
        width: 35px;
        z-index: 1000;
      }
    }

    .btn--about-profile {
      position: relative;
      margin: auto;
      margin-top: -0.6rem;
      opacity: 1;
    }

    &__social {
      margin: auto;
      // margin-block: 0;
      margin-bottom: 0;
      width: 200px;

      svg {
        width: 17px;
        path {
          stroke: var(--primary-color2);
          stroke-width: 1.1px;
        }
      }
    }
  }

  &-nav {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: var(--secondary-color);
    border-top: 1px solid var(--primary-color2-25);
    border-bottom: 1px solid var(--primary-color2-25);
    box-shadow: var(--box-shadow);
    transition: var(--longer-transition);
    transform: translateX(-100%);
    opacity: 0;

    &__link {
      font-family: var(--open-s);
      color: var(--color-text-2);
      font-weight: 700;
      opacity: 0.7;
      transition: var(--fast-transition);
      cursor: pointer;
      &.active,
      &:hover {
        opacity: 1;
        color: var(--secondary-color2);
        font-style: normal;
      }
    }
  }

  &-info {
    padding: 2rem 2rem;
    padding: 9% 9%;
    background-color: var(--secondary-color);
    box-shadow: var(--box-shadow);
    transition: var(--longer-transition);
    transform: translateY(100%);
    opacity: 0;

    &__description {
      margin: auto;
    }
    &__title {
      margin-bottom: 0.7rem;
      font-family: var(--poppins);
      color: var(--secondary-color2);
      font-weight: 700;
      font-size: 2rem;
    }
    &__text {
      font-family: var(--open-s);
      color: var(--color-text-2);
      font-size: 16px;
      line-height: 1.4;
      white-space: pre-wrap;
    }
    .message {
      margin: auto;
    }

    .about__subtitle {
      font-size: 22px;
    }

    .about__list--skills {
      margin-top: 3rem;
    }

    .about__item--hard-skills {
      margin-bottom: 3rem;
    }

    &__hard-skills {
      margin-block: 2rem 2rem;
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(50px, 1fr));
      gap: 2rem 3rem;
      align-items: flex-end;
    }

    &__hard-skill {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      list-style: none;
      opacity: 0;
      transform: translateY(110%);
      transition: var(--slow-transition);
      &.active {
        transform: translateY(0%);
        opacity: 0.9;
      }
      &:hover {
        opacity: 1;
        .hard-skill__name {
          transform: translateY(0%);
          opacity: 1;
        }
      }
    }
    #hard-skill__icon {
      height: 50px;
    }
    .hard-skill__name {
      margin-top: 0.45rem;
      font-size: 11px;
      font-family: var(--poppins);

      transition: var(--fast-transition);
      transition-timing-function: ease-in;
    }

    &__soft-skills {
      display: flex;
      flex-direction: column;
      align-items: left;
    }
    &__soft-skill {
      list-style: none;
      margin-block: 1rem 1rem;
      &:before {
        content: "\2022";
        color: var(--primary-color2);
        display: inline-block;
        width: 1em;
        margin-left: -1em;
      }
      .soft-skill__title {
        position: relative;
        color: var(--primary-color2);
        font-family: var(--poppins);
        font-weight: 400;
        &:before {
          content: "";
        }
      }
      .soft-skill__text {
        font-family: var(--open-s);
        font-size: 16px;
        font-weight: 200;
        line-height: 1.4;
        color: var(--color-text-2);
      }
    }

    &__hobbies {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
      gap: 2rem;
      margin-top: 3rem;
      .hobbie {
        display: flex;
        flex-direction: column;
        align-items: center;
        // max-width: 300px;
        margin-bottom: 1.5rem;
        text-align: center;
        #hobbie__icon {
          height: 50px;
          text-shadow: var(--box-shadow);
          path {
            fill: var(--primary-color2);
          }
        }
        &__title {
          margin-block: 1rem 0.3rem;
          font-family: var(--poppins);
          color: var(--primary-color2);
          font-weight: 600;
          font-size: 20px;
        }
        &__text {
          font-family: var(--open-s);
          color: var(--color-text-2);
          font-weight: 200;
          font-size: 16px;
          line-height: 1.4;
          max-width: 420px;
        }
      }
    }
  }
}

@keyframes fade-down {
  from {
  }
  to {
    transform: translateY(0%);
    opacity: 1;
  }
}

@keyframes fade-left {
  from {
    transform: translateX(-100%);
    opacity: 0;
  }
  to {
    transform: translateX(0%);
    opacity: 1;
  }
}

@media screen and (min-width: 800px) {
  .about {
    grid-template-columns: 360px 1fr;
    grid-template-rows: 60px minmax(100%, 640px);
    max-height: 720px;
    max-width: 1224px;
    border-radius: var(--small-border-r);
    overflow: visible;
    &-profile {
      grid-row: 2 / 3;
      grid-column: 1 / 2;
      border-right: 1px solid var(--primary-color2-50);
      border-top-left-radius: var(--big-border-r);
      border-bottom-left-radius: var(--big-border-r);
      overflow-y: auto;
      transform: translate(-100%, -100%);
    }

    &-nav {
      grid-row: 1 / 2;
      grid-column: 2 / 3;
      width: 100%;
      max-width: 520px;
      height: 100%;
      border-top: none;
      border-top-left-radius: var(--big-border-r);
      border-top-right-radius: var(--big-border-r);
      overflow: auto;
      transform: translate(100%, -100%);
    }

    &-info {
      grid-row: 2 / 3;
      grid-column: 2 / 3;
      max-height: 640px;
      border-top-right-radius: var(--big-border-r);
      border-bottom-right-radius: var(--big-border-r);
      overflow: auto;
      transform: translate(100%, 100%);

      &__hard-skill {
        &:hover {
          .hard-skill__name {
            transform: translateY(0%);
            opacity: 1;
          }
        }
      }
      .hard-skill__name {
        transform: translateY(40%);
        opacity: 0;
      }
    }
  }
}

@media screen and (min-width: 800px) and (max-height: 731px) {
  .about {
    grid-template-columns: auto;
    grid-template-rows: auto 60px auto;
    max-width: 1024px;
    overflow: auto;
    &-profile {
      grid-column: 1 / 2;
      grid-row: 1 / 2;
      border-right: none;
      border-radius: 0;
      transform: translateY(-100%);
    }
    &-nav {
      grid-column: 1 / 2;
      grid-row: 2 / 3;
      max-width: 100%;
      border-top: 1px solid var(--primary-color2-25);
      border-radius: 0;
      transform: translateX(-100%);
    }

    &-info {
      grid-column: 1 / 2;
      grid-row: 3 / -1;
      max-height: auto;
      min-height: 640px;
      border-radius: 0;
      transform: translateY(100%);
    }
  }
}

[class*="react"] {
  color: var(--react);
  path {
    fill: var(--react);
    animation: spinr 9s linear infinite;
    transform-origin: center;
  }
  @keyframes spinr {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
}

[class*="redux"] {
  color: var(--redux);
  path {
    fill: var(--redux);
  }
}

[class*="javascript"] {
  color: var(--js);
  path {
    fill: var(--js);
  }
}

[class*="css"] {
  color: var(--css);
  path {
    fill: var(--css);
  }
}

[class*="sass"] {
  color: #cd6799;

  path {
    fill: #cd6799;
  }
}

[class*="html"] {
  color: var(--html);
  path {
    fill: var(--html);
  }
}

[class*="git"] {
  color: var(--git);

  path {
    fill: var(--git);
  }
}

[class*="github"] {
  color: var(--github);

  path {
    fill: var(--github);
  }
}

[class*="figma"] {
  color: var(--figma);

  path {
    stroke-width: 0px;
  }

  path:nth-child(1) {
    fill: #f24e1e;
  }

  path:nth-child(2) {
    fill: #ff7262;
  }
  path:nth-child(3) {
    fill: #1abcfe;
  }

  path:nth-child(4) {
    fill: #0acf83;
  }
  path:nth-child(5) {
    fill: #a259ff;
  }
}

[class*="ui/ux"],
[class*="bem"] {
  color: var(--color-text);

  path {
    fill: var(--color-text);
  }
}

[class*="photoshop"] {
  color: var(--photoshop);

  path {
    fill: var(--photoshop);
  }
}

[class*="premiere"] {
  color: #7e2896;

  path {
    fill: #7e2896;
  }
}
