.game {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-content: center;
  opacity: 0;
  transition: var(--fast-transition);
  &.active {
    opacity: 1;
  }
  &.inactive {
    opacity: 0;
  }

  &__core {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    text-align: center;
  }
  &__text {
    margin-bottom: 1rem;
    font-family: var(--poppins);
    color: var(--color-text);
    font-size: clamp(0.88rem, calc(0.61rem + 1.33vw), 1.25rem);
    line-height: 1.4;
    animation: none;
    white-space: pre-wrap;
  }
  &__text.a {
    animation: titilear 4s infinite;
    @keyframes titilear {
      0% {
        opacity: 0;
      }
      10% {
        opacity: 1;
      }
      20% {
        opacity: 0;
      }
      30% {
        opacity: 1;
      }
      40% {
        opacity: 0;
      }
      50% {
        opacity: 1;
      }
      60% {
        opacity: 0;
      }
      70% {
        opacity: 1;
      }
      80% {
        opacity: 0;
      }
      90% {
        opacity: 1;
      }
      100% {
        opacity: 0;
      }
    }
  }
  &__btn {
    animation: btn 1s ease-in-out forwards;
    transform: translateY(0%);
    transition: transform 0.2s ease-in-out;
  }
}

.game.b {
  .game__btn {
    transform: translateY(200%);
  }
}

.game.c {
  .game__btn {
    transform: translateY(-400%);
  }
}
