.nav {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  background-color: var(--primary-color);
  border-top: 1px solid var(--primary-color2-25);
  // box-shadow: 0px 0px 7px rgba(20, 20, 20, 0.5);

  &__links {
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }

  &__link {
    display: flex;
    align-items: center;
    opacity: 0;
    transform: translateX(-200%);
    animation: left-right 0.3s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;

    #nav__icon {
      width: 26px;
      transition: var(--fast-transition);
      path {
        fill: var(--color-text-2);
        opacity: 0.7;
        transition: var(--fast-transition);
      }
    }

    &.active,
    &:active {
      #nav__icon path {
        opacity: 1;
        fill: var(--primary-color2);
      }
    }
  }

  &__link--projects {
    animation-delay: 0.2s;
  }
  &__link--playground {
    animation-delay: 0.4s;
  }
  &__link--contact {
    animation-delay: 0.6s;
  }

  &__link--settings {
    animation-delay: 0.8s;
  }

  &__tag {
    display: none;
  }

  &__link--settings {
    position: relative;
  }

  // &__settings {
  //   // display: none;
  // }

  &__link--settings {
    &:hover {
      .settings {
        visibility: visible;
        opacity: 1;
        bottom: 130%;
      }
    }
  }
  &__settings {
    position: absolute;
    right: 0;
    bottom: 0%;
    visibility: hidden;
    opacity: 0;
    transition: var(--fast-transition);
  }
}

.settings {
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: var(--box-small-shadow);
  background-color: var(--secondary-color-98);
  border-radius: var(--small-border-r);
  &__transparent {
    position: absolute;
    z-index: -1;
    right: 0;
    top: 0;
    width: 70%;
    height: 200%;
    background-color: transparent;
  }

  &__language {
    position: relative;
    display: flex;
    align-items: center;
    padding: 0.5rem 1rem;
    cursor: pointer;
    #settings__flag {
      width: 30px;
      cursor: pointer;
    }
    .language-expand {
      position: absolute;
      right: -0.3px;
      font-size: 18px;
      opacity: 0.8;
      transition: var(--fast-transition);
    }
    &:hover {
      .settings {
        border-top-left-radius: 0px;
      }
      .language-expand {
        opacity: 1;
        color: var(--primary-color2);
        transform: rotate(180deg);
      }
      .settings__l-options {
        bottom: 100%;
        opacity: 1;
      }
    }
  }

  &__l-options {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: -1;
    display: flex;
    justify-content: center;
    width: 100%;
    height: auto;
    padding: 0.5rem 0rem;
    border-top-left-radius: var(--small-border-r);
    border-top-right-radius: var(--small-border-r);
    background-color: var(--secondary-color-98);
    transition: var(--fast-transition);

    opacity: 0;
    #settings__flag {
      width: 30px;

      cursor: pointer;
      transition: var(--fast-transition);
      &:hover {
        transform: scale(1.1);
      }
    }
  }
}

@keyframes left-right {
  from {
    opacity: 0;
    transform: translateX(-200%);
  }
  to {
    opacity: 1;
    transform: translateX(0%);
  }
}

@media screen and (min-width: 770px) {
  .nav {
    top: 0;
    left: 0;
    height: 100%;
    width: fit-content;
    display: flex;
    align-items: center;
    background: none;
    border-top: none;

    &__links {
      flex-direction: column;
      align-items: center;
      width: var(--general-padding);
      height: 100%;
      max-height: 450px;
      border-top: none;
    }

    &__link {
      position: relative;
      &:hover {
        #nav__icon {
          transform: translateX(-15%);
          path {
            fill: var(--primary-color2);
          }
        }
        .nav__tag {
          visibility: visible;
          opacity: 1;
          transform: translateX(2%);
        }
      }
    }

    &__icon {
      transition: var(--fast-transition);
    }

    &__tag {
      position: absolute;
      top: 0;
      bottom: 0;
      margin: auto auto;
      left: 110%;
      display: flex;
      align-items: center;
      padding: 1.2rem 0.7rem;
      font-family: var(--poppins);
      color: var(--primary-color2);
      font-weight: 700;
      background-color: var(--secondary-color-98);
      border-radius: var(--small-border-r);
      box-shadow: var(--box-small-shadow);
      transform: translateX(-25%);
      visibility: hidden;
      opacity: 0;
      transition: var(--fast-transition);
    }

    &__link--settings {
      padding: 0.5rem 0.7rem;
      #nav__icon {
        position: relative;
        z-index: 10;
      }

      &:hover {
        .settings {
          visibility: visible;
          opacity: 1;
          bottom: 0;
          // left: 84%;
          transform: translateX(57%);
        }
      }
    }
    &__settings {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      margin: auto 0;
      visibility: hidden;
      opacity: 0;
      width: fit-content;
      transition: var(--fast-transition);
    }

    .settings__transparent {
      top: 0;
      bottom: 0;
      left: -50%;
      margin: auto;
      width: 160%;
      height: 130%;
    }
  }
}

@media screen and (min-width: 1225px) {
  .nav__links {
    width: calc((100vw - 1024px) / 2);
  }
}
