h1 {
  margin: 0;
}

.header {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  height: 100vh;
  margin-bottom: var(--section-margin);

  &__logo-container {
    opacity: 0.8;
    transition: var(--fast-transition);
    &:hover {
      opacity: 1;
    }
  }
  &__logo {
    position: absolute;
    top: clamp(15px, 5%, 25px);
    width: var(--fs-xxl);
    max-width: 60px;
  }

  &__container {
    width: 100%;
  }

  &__title {
    font-family: var(--poppins);
    font-size: var(--fs-xxl);
    color: var(--color-text);
  }
  &__html {
    display: block;
    font-family: var(--open-s);
    font-size: var(--fs-xs);
    font-weight: 400;
    font-style: italic;
    color: var(--primary-color2);
    &::after {
      content: ">";
      color: var(--primary-color2);
    }
    &::before {
      content: "<";
      color: var(--primary-color2);
    }
    .light-blue {
      color: #4297cd;
    }
    .orange {
      color: var(--secondary-color2);
    }
  }

  &__description {
    position: relative;
    display: flex;
    align-items: end;
    // margin-block: 5px 35px;
    height: clamp(1.25rem, calc(1.03rem + 1.11vw), 1.56rem);
    color: var(--color-text-2);
    font-family: "Open Sans", sans-serif;
    font-size: var(--fs-m);
    overflow: hidden;
    // border: 1px solid white;
  }

  &__words {
    position: relative;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: start;
    text-align: center;
    width: 8ch;
    height: 100%;
    font-weight: 600;
    color: var(--secondary-color2);
    animation: sp 4s alternate infinite;
    animation-delay: 1.8s;
    .a-word,
    .b-word {
      right: 0;
      left: 0;
      margin: auto;
      position: absolute;
      height: 100%;
      display: flex;
      align-items: flex-end;
      justify-content: center;
    }
    .a-word {
      top: 100%;
    }

    @keyframes sp {
      0% {
        width: 8ch;
        transform: translateY(0%);
      }
      50% {
        width: 8ch;
        transform: translateY(0%);
      }
      60% {
        width: 6ch;
        transform: translateY(-100%);
      }
      75% {
        width: 6ch;
        transform: translateY(-100%);
      }
      100% {
        width: 6ch;
        transform: translateY(-100%);
      }
    }
  }
}

.btn--header {
  margin-top: 2rem;
  &:after {
    content: url("data:image/svg+xml,%3Csvg width='10' height='10' viewBox='0 0 15 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.788086 1.67691L2.19809 0.266907L12.7881 10.8569V4.26691H14.7881V14.2669H4.78809V12.2669H11.3781L0.788086 1.67691Z' fill='%23ba593b'/%3E%3C/svg%3E%0A");
    position: absolute;
    bottom: -20px;
    left: -20px;
    animation: signal 0.6s infinite alternate;
    animation-timing-function: linear;
    opacity: 0.9;

    @keyframes signal {
      from {
        transform: translate(5px, -5px) rotate(280deg);
      }
      to {
        transform: translate(0px, 0px) rotate(280deg);
      }
    }
  }
  @media screen and (min-width: 770px) {
    &:after {
      content: url("data:image/svg+xml,%3Csvg width='15' height='15' viewBox='0 0 15 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.788086 1.67691L2.19809 0.266907L12.7881 10.8569V4.26691H14.7881V14.2669H4.78809V12.2669H11.3781L0.788086 1.67691Z' fill='%23ba593b'/%3E%3C/svg%3E%0A");
      bottom: -30px;
      left: -30px;
    }

    @keyframes signal {
      from {
        transform: translate(9px, -9px) rotate(280deg);
      }
      to {
        transform: translate(0px, 0px) rotate(280deg);
      }
    }
  }
}

@mixin animation($name, $time) {
  animation: $name $time infinite;
  animation-timing-function: ease-in-out;
  animation-direction: alternate;
}

#computer__logo {
  position: absolute;
  right: 0;
  bottom: 20%;
  z-index: -1;
  opacity: 0;
  transform: translateY(200%);
  width: clamp(11.25rem, calc(4.17rem + 35.4vw), 31.25rem);
  height: auto;
  animation: opacity 3s ease-out;
  animation-delay: 2000ms;
  animation-fill-mode: forwards;
}

@keyframes opacity {
  from {
    opacity: 0;
    transform: translateY(200%);
  }
  to {
    opacity: 1;
    transform: translateY(0%);
  }
}

#computer {
  @include animation(float, 3s);
  filter: drop-shadow(5px 5px 3px #14141480);
}

#right-cloud {
  @include animation(right-cloud, 3s);
}

#left-cloud {
  @include animation(left-cloud, 3s);
}

#bottom-cloud {
  @include animation(bottom-cloud, 3s);
}

#moon path {
  fill: var(--primary-color2);
}

#computer_2 {
  transform: scale(1);
}

#light rect {
  position: absolute;
  top: 0;
  transform-origin: center;
  transform-box: fill-box;
  opacity: 1;
  @include animation(light, 2s);
}

#light rect {
  transform-origin: center;
  transform-box: fill-box;
  // animation: light 2s infinite;
  animation-timing-function: ease-in;
  animation-direction: alternate;
}

@keyframes light {
  from {
    transform: scale(0.7);
    fill: var(--primary-color2-50);
  }
  to {
    fill: var(--primary-color2);
    transform: scale(0.9);
  }
}

@keyframes float {
  from {
    transform: translateY(0px);
  }

  to {
    transform: translateY(10px);
  }
}
@keyframes right-cloud {
  from {
    transform: translatex(0px);
  }

  to {
    transform: translatex(3px);
  }
}

@keyframes left-cloud {
  from {
    transform: translatex(0px);
  }

  to {
    transform: translatex(-3px);
  }
}

@keyframes bottom-cloud {
  from {
    transform: translateY(0px);
  }

  to {
    transform: translateY(3px);
  }
}

#react,
#js,
#css,
#html {
  transform-origin: center;
  transform-box: fill-box;
  transform: scale(0.5);
  transition: all 0.2s ease-in-out;
  opacity: 0;
}

#react.active,
#js.active,
#css.active,
#html.active {
  visibility: visible;
  transform: scale(1.1);
  opacity: 1;
}

#react path,
#js path,
#css path,
#html path {
  filter: drop-shadow(0px 0px 8px var(--react));
  stroke-width: 0px;
}
#js path {
  filter: drop-shadow(0px 0px 8px var(--js));
}
#css path {
  filter: drop-shadow(0px 0px 8px var(--css));
}
#html path {
  filter: drop-shadow(0px 0px 8px var(--html));
}
