:root {
  --primary-color: #151b25;
  --primary-color-98: #151b25fa;
  --primary-color2: #3487d1;
  --primary-color2-50: #3487d180;
  --primary-color2-25: #3487d140;
  //
  --secondary-color: #18191d;
  --secondary-color-98: #18191de6;
  --secondary-color-60: #18191d;
  --secondary-color2: #ba593b;
  --secondary-color3: #764abc;
  --secondary-color4: #ffe70d;
  --white: rgba(255, 255, 255, 0.92);
  --white-25: #ffffff40;
  //
  --color-text: #e1ebfc;
  --color-text-2: #bbc8de;
  --inactive: #8e8e92;
  //
  --react: #61dafb;
  --redux: #764abc;
  --html: #f16529;
  --css: #3487d1;
  --js: #ffe70d;
  --figma: #0acf83;
  --git: #f1502f;
  --github: #6cc644;
  --photoshop: #31a8ff;
  --else: #ffffff;
  //
  --poppins: "Poppins", sans-serif;
  --open-s: "Open Sans", sans-serif;
  //
  --fs-xxl: clamp(2.81rem, calc(1.37rem + 7.19vw), 6.88rem);
  --fs-m: clamp(0.88rem, calc(0.81rem + 0.33vw), 1.06rem);
  --fs-t: clamp(0.88rem, calc(0.74rem + 0.66vw), 1.25rem);
  --fs-s: clamp(0.75rem, calc(0.64rem + 0.57vw), 1rem);
  --fs-xs: clamp(0.69rem, calc(0.58rem + 0.55vw), 1rem);
  //
  --general-padding: clamp(1.56rem, calc(-1.77rem + 16.67vw), 6.25rem);
  --max-width: 1024px;
  --content-width: 900px;
  //
  --margin-i: 50px;
  --section-margin: 60px;
  //
  --big-border-r: 10px;
  --small-border-r: 5px;
  //
  --box-shadow: rgba(0, 0, 0, 0.25) 0px 5px 15px;
  --box-small-shadow: rgba(0, 0, 0, 0.25) 0px 3px 010px;
  //
  --fast-transition: all 0.15s ease-in-out;
  --slow-transition: all 0.5s ease-in-out;
  --btn-bg: transparent;
}

@media screen and (min-width: 480px) {
  :root {
    --margin-i: 80px;
  }
}

@media screen and (min-width: 770px) {
  :root {
    --general-padding: clamp(6.25rem, calc(16.85rem + -22.03vw), 0rem);
    --margin-i: 120px;
  }
}

@media screen and (min-width: 1225px) {
  :root {
    --general-padding: 0px;
  }
}
