.project {
  position: relative;
  display: flex;
  flex-direction: column;
  width: clamp(280px, 100%, 750px);
  height: auto;
  margin-bottom: var(--margin-i);
  margin-inline: auto;
  box-shadow: var(--box-shadow);
  border-radius: var(--small-border-r);
  --info-padding-b: 25px;
  --info-padding-i: 15px;
  &__img {
    position: relative;
    display: flex;
    img {
      width: 100%;
      border-radius: var(--small-border-r) var(--small-border-r) 0 0;
      opacity: 0.7;
      transition: var(--fast-transition);
    }
    &::before {
      content: "";
      position: absolute;
      z-index: 1;
      width: 100%;
      height: 100%;
      border-radius: var(--small-border-r) var(--small-border-r) 0 0;
      background-color: #3487d1;
      opacity: 0.2;
      transition: var(--fast-transition);
    }
  }
  &__img.project__img--open,
  &:hover &__img {
    img {
      opacity: 1;
    }
    &::before {
      opacity: 0;
    }
  }

  &__info {
    position: relative;
    display: flex;
    flex-direction: column;
    padding-block: var(--info-padding-b) 10px;
    padding-inline: var(--info-padding-i);
    border-radius: 0 0 var(--small-border-r) var(--small-border-r);
    background-color: var(--secondary-color);
  }

  &__core {
    margin-bottom: 1rem;
  }

  &__title {
    margin-top: 10px;
    margin-bottom: 10px;
    color: var(--text-color);
    font-family: "Poppins", sans-serif;
    font-weight: 600;
    font-size: clamp(1.5rem, 1.32rem + 0.88vw, 2rem);
  }

  &__description {
    max-width: 95%;
    font-family: var(--open-s);
    font-size: 16px;
    color: var(--color-text-2);
    line-height: 1.5;
  }

  &__details {
    display: flex;
    align-items: center;
    justify-content: end;
    width: 100%;
    margin-block: 0.2rem 1rem;
    margin-right: 4rem;
    color: var(--secondary-color2);
    text-align: center;
    font-family: "Open Sans", sans-serif;
    font-size: 14px;
    font-weight: 600;
    opacity: 0.8;
    transition: opacity 0.2s ease-in-out;
    cursor: pointer;
    span {
      font-size: 0.8rem;
    }
    &:hover {
      opacity: 1;
    }
  }

  &__technologies {
    width: 100%;
    order: -1;
    margin-bottom: 0.5rem;
    overflow: hidden;
  }

  &__list {
    display: flex;
    // justify-content: space-around;
    list-style: none;
  }

  &__list--open {
    flex-wrap: wrap;
  }

  &__technology {
    padding: 0.18rem 0.7rem 0.18rem 0;
    margin-right: 1rem;
    text-align: center;
    font-size: clamp(0.69rem, calc(0.63rem + 0.28vw), 0.81rem);
    font-family: var(--open-s);
    font-weight: 600;
    color: var(--else);
    background-color: var(--secondary-color-60);
    border-radius: var(--small-border-r);
    opacity: 0.8;
  }

  &__links {
    width: 100%;
    display: flex;
    justify-content: end;
    justify-content: start;
  }

  &__link {
    margin-right: 1.5rem;
    opacity: 0.8;
    transition: var(--fast-transition);
    svg {
      width: 19px;
      height: 19px;
      fill: none;
    }
    #project__github {
      stroke: var(--white);
      stroke-width: 1.8px;
    }
    #project__external {
      fill: var(--white);
    }
    &:hover {
      & {
        opacity: 1;
        transform: scale(1.05);
      }
      #project__github {
        stroke: var(--primary-color2);
      }
      #project__external {
        fill: var(--primary-color2);
      }
    }
  }

  &:hover {
    .project__technology {
      opacity: 1;
    }
  }
}

@media screen and (min-width: 770px) {
  .project {
    display: grid;
    align-items: center;
    justify-content: center;
    grid-template-columns: repeat(12, 1fr);
    box-shadow: none;
    width: 100%;
    --info-padding-b: 15px;
    --info-padding-i: 15px;
    &__img {
      grid-area: 1 / 1;
      grid-column: 1 / 7;
      position: relative;
      width: 100%;
      transition: var(--fast-transition);
      border-radius: var(--small-border-r);
      img {
        border-radius: var(--small-border-r);
        box-shadow: var(--box-shadow);
      }
      &::before {
        border-radius: var(--small-border-r);
        width: 100%;
        height: 100%;
      }
      &:hover {
        img {
          transform: translateX(-8%);
        }
        &:before {
          transform: translateX(-8%);
        }
      }
    }

    &__info {
      position: relative;
      padding: 0;
      grid-row: 1 / 1;
      grid-column: -1 / 5;
      z-index: 10;
      width: 100%;
      text-align: left;
      border-radius: 5px;
      background-color: transparent;
      box-shadow: none;
    }

    &__technologies {
      position: absolute;
      bottom: 100%;
    }

    &__list {
      justify-content: end;
    }

    &__technology {
      padding: 0.18rem 0.7rem;
      margin-left: 0.5rem;
      margin-right: 0;
    }

    &__core {
      margin: 0;

      padding: var(--info-padding-b) 25px;
      background-color: var(--secondary-color);
      border-radius: var(--small-border-r);
      box-shadow: var(--box-shadow);
    }

    &__links {
      position: absolute;
      top: 100%;
      right: 0;
      justify-content: end;
      margin-top: 0.5rem;
    }

    &__link {
      margin: 0.18rem 0.4rem 0.18rem 0.7rem;
    }
  }

  .project:nth-child(even) {
    .project__img {
      grid-area: 1 / 1;
      grid-column: -1 / 7;
      &:hover {
        img {
          transform: translateX(8%);
        }
        &:before {
          transform: translateX(8%);
        }
      }
    }
    .project__info {
      position: relative;
      padding: 0;
      grid-row: 1 / 1;
      grid-column: 1 / 9;
      z-index: 10;
      width: 100%;
      text-align: left;
      border-radius: 5px;
      background-color: transparent;
      box-shadow: var(--box-shadow);
    }

    .project__technologies {
      right: auto;
    }
    .project__technology {
      .project__technology {
        margin-left: 0;
        margin-right: 0.5rem;
      }
    }

    .project__list {
      justify-content: start;
    }

    .project__links {
      justify-content: start;
      margin-top: 0.5rem;
    }

    .project__link {
      margin: 0.18rem 0.4rem 0.18rem 0.7rem;
    }
  }
}
