@import "normalize";
@import "variables";

body {
  position: relative;
  background-color: var(--primary-color);
  scroll-behavior: smooth !important;
  transition: var(--slow-transition);
}

body.modal--open {
  overflow: hidden;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
  color: white;
}

svg {
  overflow: visible;
}
p {
  letter-spacing: 0.1px;
}

#root {
  position: relative;
  z-index: 10;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  max-width: var(--max-width);
  height: auto;
  padding: 0 var(--general-padding);
  margin: auto;
}

#particles-js {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  filter: blur(1px);
}

.content {
  width: 100%;
  max-width: var(--content-width);
  padding: var(--general-padding);
}

.btn {
  position: relative;
  width: fit-content;
  padding: 0.9rem 1.5rem;
  border: none;
  border-radius: var(--small-border-r);
  color: var(--color-text);
  color: var(--primary-color2);
  font-family: var(--open-s);
  font-size: var(--fs-s);
  font-size: 14px;
  font-weight: 500;
  line-height: 1;
  background-color: var(--btn-bg);
  box-shadow: var(--box-small-shadow);
  outline: solid 1px var(--primary-color2);
  cursor: pointer;
  transition: var(--fast-transition);
  opacity: 0;
  // animation: btn 0.5s ease-in-out forwards;
  @media screen and (min-width: 770px) {
    & {
      padding: 1rem 1.8rem;
      font-size: 16px;
    }
  }

  &:active {
    outline: solid 2px var(--secondary-color2);
  }

  &:hover {
    background: var(--primary-color2-25);
  }

  @keyframes btn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
}

.message {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 600px;
  margin-top: -22px;
  margin-inline: auto;
  padding-block: clamp(50px, 10%, 80px);
  text-align: center;
  border: 1px solid var(--primary-color2);
  border-radius: var(--small-border-r);
  white-space: pre-wrap;
  opacity: 0;
  animation: msg 0.6s ease-in-out forwards;
  animation-delay: 1s;
  @media screen and (min-width: 770px) {
    margin-top: -40px;
  }

  #message__icon {
    width: 70px;
    margin-bottom: 1rem;
    path {
      fill: var(--secondary-color2);
    }
  }

  &__text {
    width: 80%;
    max-width: 350px;
    color: var(--secondary-color2);
    line-height: 1.4;
    font-family: var(--open-s);
    font-weight: 700;
    @media screen and (min-width: 770px) {
      font-size: 18px;
    }
  }

  @keyframes msg {
    from {
      transform: scale(0.5);
      opacity: 0;
    }
    to {
      transform: scale(1);
      opacity: 1;
    }
  }
}

@import "nav";
@import "header";
@import "main";
@import "projects";
@import "modal";
@import "game";

.react {
  color: var(--react);
}
.redux {
  color: var(--redux);
}
.html {
  color: var(--html);
}
.css {
  color: var(--css);
}
.js {
  color: var(--js);
}
.figma {
  color: var(--figma);
}
.apis {
  color: var(--else);
}
.hammer {
  path {
    fill: var(--orange);
  }
}

.remark {
  color: var(--secondary-color2);
  text-decoration: none;
}
a.remark {
  position: relative;
  font-weight: bold;
  overflow: hidden;

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: var(--secondary-color);
    transform-box: fill-box;
    transform: scaleX(0);
    transform-origin: left;
    transition: var(--fast-transition);
  }
  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 1px;
    background-color: var(--secondary-color2);
    transform-box: fill-box;
    transform: scaleX(0);
    transform-origin: left;
    transition: var(--fast-transition);
  }
  &:hover {
    &::before {
      transform: scale(1);
    }
    &::after {
      transform: scale(1);
      transition-delay: 0.2s;
    }
  }
}

.remark2 {
  color: var(--primary-color2);
  text-decoration: none;
  font-weight: bold;
}
.remark.nobold,
.remark2.nobold {
  font-weight: 400;
}

.nav,
.header,
.main {
  transition: all 1.2s ease-in-out;
}

.nav.open-modal,
.header.open-modal,
.main.open-modal {
  opacity: 0;
}
